  position: absolute;
  top: 50%;
  width: 24px;
}

#pp_full_res .pp_inline {
  text-align: left;
}

div.ppt {
  color: #fff !important;
  font-weight: 700;
  display: none;
  font-size: 17px;
  margin: 0 0 5px 15px;
  z-index: 9999;
}